* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #f1f51a;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-top: 20px;
  margin-bottom: 16px;
  margin-left: 40px;
}

.img {
  width: 200px;
  height: 100px;
}

.flag {
  width: 100px;
  height: 50px;
}

li {
  display: flex;
  align-items: center;
  margin-left: 40px;
  padding: 8px;
}
